import React, { useState,useEffect } from 'react';
import {Form, Input, Button, Switch, Card, Steps, Result, Divider} from 'antd';
import { useLocation } from 'react-router-dom';
import {
    UserOutlined,
    SolutionOutlined,
    SmileOutlined,
    CheckOutlined,
} from '@ant-design/icons';

const { Step } = Steps;

const MultiStepForm = () => {
    const [currentStep, setCurrentStep] = useState(0);
    const [formValues, setFormValues] = useState({
        participation: false,
        bibleStudies: 0,
        hours: 0,
    });

    const [isRegularPublisher, setRegularPublisher] = useState(false);
    useEffect(() => {
        // Replace this check with the appropriate condition based on your Telegram WebApp data
        if (window.Telegram && window.Telegram.WebApp) {
            window.Telegram.WebApp.ready();

            let paramValue = window.Telegram.WebApp.initDataUnsafe.start_param

            setRegularPublisher(paramValue === "regular")
        }
    }, []);

    const next = () => {
        setCurrentStep(currentStep + 1);
    };

    const prev = () => {
        setCurrentStep(currentStep - 1);
    };

    const onChangeParticipation = (checked) => {
        setFormValues({ ...formValues, participation: checked });
    };

    const onChangeBibleStudies = (e) => {
        setFormValues({ ...formValues, bibleStudies: parseInt(e.target.value, 10) });
    };

    const onSubmit = () => {
        setCurrentStep(2); // Proceed to the final step

        if (window.Telegram && window.Telegram.WebApp) {
            setTimeout(() => {
                window.Telegram.WebApp.sendData(JSON.stringify(formValues));
            }, 3000);
        }
    };

    const getIcon = (index) => {
        // if (index === currentStep) {
        //     return <LoadingOutlined />;
        // } else if (index < currentStep) {
        if (index < currentStep) {
            return React.cloneElement(stepIcons[index], { style: { color: 'green' } });
        }
        return stepIcons[index];
    };

    const onChangeHours = (e) => {
        setFormValues({ ...formValues, hours: parseInt(e.target.value, 10) });
    };


    const stepIcons = [
        <UserOutlined />,
        <SolutionOutlined />,
        <SmileOutlined />,
    ];

    return (

        <Card>
            {/*<Steps current={currentStep}>*/}
            {/*    <Step icon={getIcon(0)} />*/}
            {/*    <Step icon={getIcon(1)} />*/}
            {/*    <Step icon={getIcon(2)} />*/}
            {/*</Steps>*/}
            <Form
                onFinish={onSubmit}
                initialValues={formValues}
                style={{ padding: '30px 15px 20px' }}
                labelCol={{ span: 12 }}
                wrapperCol={{ span: 10 }}
                layout="horizontal"
            >
                {currentStep === 0 && (
                    <>
                        {isRegularPublisher ? (
                            <Form.Item label=" :">
                                <Input
                                    type="number"
                                    value={formValues.hours}
                                    onChange={onChangeHours}
                                    min="0"
                                />
                            </Form.Item>
                        ) : (
                            <Form.Item label="  :">
                            <Switch checked={formValues.participation} onChange={onChangeParticipation} />
                             </Form.Item>
                            )
                        }
                        <Form.Item label="  :">
                            <Input
                                type="number"
                                value={formValues.bibleStudies}
                                onChange={onChangeBibleStudies}
                                min="0"
                            />
                        </Form.Item>
                        <Button type="primary" ghost onClick={next} style={{width: "100%", marginTop: 35}}>
                            
                        </Button>
                    </>
                )}
                {currentStep === 1 && (
                    <>
                        <p><i>   ,  -           ""</i></p>
                        <Divider orientation="left">  :</Divider>
                        <div style={{fontWeight: "bold"}}>
                            {isRegularPublisher ? (
                            <p> : {formValues.hours}</p>
                        ) : (
                            <p>  : {formValues.participation ? '' : ''}</p>
                        )}
                        <p>  : {formValues.bibleStudies}</p>
                        </div>
                        <div style={{marginTop: 25, width: "100%", display: "flex"}}>
                            <Button style={{ margin: '0 8px', flexGrow: 1 }} onClick={prev}>
                                
                            </Button>
                            <Button type="primary" htmlType="submit" style={{flexGrow: 1, background: 'green'}}>
                                 <CheckOutlined />
                            </Button>
                        </div>
                    </>
                )}
                {currentStep === 2 && (
                    <Result
                        status="success"
                        title="  !"
                        subTitle="     !"
                    />
                )}
            </Form>
        </Card>
    );
};

export default MultiStepForm;
